#content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 2rem;
}

#variable {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* width: 40%; */
  -ms-flex: 5 1;
      flex: 5 1;
}

#d1 {
  -ms-flex: 1 1;
      flex: 1 1;
}

#value {
  -ms-flex: 6 1;
      flex: 6 1;
}

@media (max-width: 768px) {
  #content {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  #variable {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    -ms-flex-positive: 0;
        flex-grow: 0;
  }
  
  #d1 {
    -ms-flex-order: 2;
        order: 2;
    -ms-flex-positive: 0;
        flex-grow: 0;
  }

  #value {
    -ms-flex-positive: 0;
        flex-grow: 0;
  }
}


